<template>
	<audio controls hidden :src="require('./asd.mp3')" ref="audio1"></audio>
	<audio controls hidden :src="require('./end.mp3')" ref="audio2"></audio>
	<div class="w-[100vw] h-[100vh] flex items-center justify-center">
		<div class="border border-[#18a058] rounded-xl p-5 flex flex-col items-center">
			<div class="flex items-center">
				<p>倒计时：</p>
				<p class="text-[100px] text-[#18a058]">{{(Math.floor(time/60)+'分'+time%60+'秒')}}</p>
			</div>
			<div @click="() => onPressStart()" class="border border-[#18a058] rounded-md px-2 py-1 text-[#18a058] text-[20px] mb-2 cursor-pointer">开始</div>
			<div class="flex items-center mb-2">
				<p class="w-[120px]">计时时间：</p>
				<n-input-number v-model:value="value1" placeholder="请输入" clearable />
				<p class="px-1">分</p>
				<n-input-number v-model:value="value2" placeholder="请输入" clearable />
				<p class="px-1">秒</p>
			</div>
			<div class="flex items-center">
				<p class="w-[120px]">剩余提醒时间：</p>
				<n-input-number v-model:value="value11" placeholder="请输入" clearable />
				<p class="px-1">分</p>
				<n-input-number v-model:value="value12" placeholder="请输入" clearable />
				<p class="px-1">秒</p>
			</div>
		</div>
		
	</div>
</template>
<script>
export default {
	components: {

	},
	watch: {

	},
	data() {
		return {
			timer: null,
			time: 0,
			value1: 1,
			value2: 0,
			value11: 1,
			value12: 0,
		}
	},
	created() {

	},
	mounted() {
		
	},
	methods: {
		onPressStart() {
			if (Number(this.value1*60)+Number(this.value2) === 0) {
				alert('请完善计时时间')
				return
			}	
			if (Number(this.value11*60)+Number(this.value12) === 0) {
				alert('请完善剩余提醒时间')
				return
			}
			clearInterval(this.timer);
			this.time = Number(this.value1*60)+Number(this.value2);
			this.timer = setInterval(() => {
				this.time--
				if (this.time === Number(this.value11*60)+Number(this.value12)) {
					this.playMusic(1)
				}
				if (this.time === 0) {
					this.playMusic(2)
					clearInterval(this.timer);
				}
			}, 1000);
		},
		playMusic(x){
			this.$refs['audio'+x].play()
		},
	},
	// 页面销毁的时候
	destroyed() {

    },
};
</script>
<style lang="less" scoped>

</style>